import {Button} from 'antd';
import { useNavigate } from 'react-router-dom';

function MainPages() {
    const navigate = useNavigate()
  return (
    <main>
        <div className='container'>
        <img src="https://i.postimg.cc/nLbrhzrS/photo-2022-06-12-01-09-04.jpg" alt="logo" />
        <h2>   !</h2>
            <div className="container__block">
            <div className='block_login'>
              <Button className='btn' onClick={()=>navigate('/login')}></Button>
            </div>
            <div className='block_registration'>
             <Button className='btn'  onClick={()=>navigate('/registration')}> </Button>
            </div>
            </div>
        </div>
    </main>
  )
}

export default MainPages